header {
  background-color: var(--primary-color);
  height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
header ul {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 85vw;
  height: 100%;
  flex-direction: row;
}
header ul li {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: var(--text-color);
  width: 10rem;
}
header ul li:hover {
  color: var(--text-color-light);
  background-color: var(--secondary-color);
}

.hero__section {
  width: 100%;
  height: auto;
}
.hero__section .title__side {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 25vh;
  width: 100%;
}
.hero__section .title__side h2 {
  font-size: 5rem;
}
.hero__section .title__side h2 strong {
  font-weight: bolder;
  color: var(--text-color-dark);
}
.hero__section .banner__holder {
  width: 100%;
  height: 50vw;
  max-height: 65vh;
  background-size: cover;
  background-image: url("/res/slider1.jpg");
}

.card__section {
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  width: 100%;
  display: inline-grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 0.5rem;
  justify-items: center;
  align-items: center;
  justify-content: center;
  align-content: center;
}
.card__section :nth-child(1) .img__holder {
  background-image: url(/res/pic1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.card__section :nth-child(2) .img__holder {
  background-image: url(/res/pic2.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.card__section :nth-child(3) .img__holder {
  background-image: url(/res/pic3.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.card__section .card {
  width: 20rem;
  max-width: 20rem;
}
.card__section .card .subtitle__holder {
  height: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--secondary-color);
}
.card__section .card .subtitle__holder h5 {
  color: var(--text-color-light);
}
.card__section .card .img__holder {
  height: 20vh;
}
.card__section .card .paragraph__holder {
  padding: 1rem;
  height: auto;
  background-color: var(--tertiary-color);
}
.card__section .card .paragraph__holder p {
  color: var(--text-color-light);
  line-height: 1.5rem;
}
.card__section .card .btn__container {
  background-color: var(--tertiary-color);
  height: auto;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}
.card__section .card .btn__container i {
  height: 4rem;
  width: 4rem;
  transition: cubic-bezier(0.55, 0.085, 0.68, 0.53);
  background-image: url(/res/plus_btn.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.card__section .card .btn__container i:hover {
  transition: cubic-bezier(0.55, 0.085, 0.68, 0.53);
  background-image: url(/res/plus_btn_h.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

@media (max-width: 1080px) {
  .card__section {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
  }
}
@media (max-width: 675px) {
  .card__section {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr 1fr;
  }
}
:root {
  --primary-color: #FFFFFF;
  --secondary-color: #662f32;
  --tertiary-color: #645955;
  --text-color: #000000;
  --text-color-light: #f5f5f5;
  --text-color-dark: #662f32;
}

* {
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--tertiary-color);
}

li {
  list-style-type: none;
}

section {
  background-color: var(--primary-color);
}

main {
  width: 60%;
  min-width: 72rem;
}

@media (max-width: 1153px) {
  main {
    width: 100%;
    min-width: auto;
  }
}